@import url('assets/lib/animate.css');
@import url('assets/css/bootstrap.css');
.App {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
}
.App .App-header {
  background: #444;
}
.App .App-content {
  -ms-flex: 1 1;
      flex: 1 1;
  width: 90%;
  max-width: 1400px;
  min-width: 960px;
  margin: 15px auto;
}
.App .App-content a {
  text-decoration: none;
}
.App .App-content .sidebar {
  width: 290px;
  font-size: 14px;
  float: right;
  margin-bottom: 20px;
}
.App .App-content .content {
  padding: 0;
  margin-right: 305px;
}
.App .App-footer {
  background: #fff;
  text-align: center;
}
.App .pull-left {
  float: left;
}
.App .pull-right {
  float: right;
}



