.panel {
  background: #fff;
}
.panel .topic-header {
  padding: 10px;
  border-radius: 3px 3px 0 0;
  border-bottom: 1px solid #e5e5e5;
}
.panel .topic-header .topic-title {
  font-size: 22px;
  font-weight: 700;
  margin: 8px 0;
  display: inline-block;
  vertical-align: bottom;
  width: 75%;
  line-height: 130%;
}
.panel .topic-header .topic-title .topic-tab-type {
  margin-right: 6px;
  background: #80bd01;
  padding: 2px 4px;
  border-radius: 3px;
  color: #fff;
  font-size: 12px;
}
.panel .topic-header .topic-title-info {
  font-size: 12px;
  color: #838383;
}
.panel .topic-header .topic-title-info span:before {
  content: "•";
  margin-right: 4px;
}
.panel .topic-content {
  padding: 10px;
  margin: 0 10px;
}
.panel .header {
  padding: 10px;
  background-color: #f6f6f6;
  border-radius: 3px 3px 0 0;
}
.panel .header .col-code {
  color: #444;
}



