.reply {
  padding: 10px;
  background: #fff;
  border-top: 1px solid #f0f0f0;
  position: relative;
  font-size: 14px;
}
.reply.reply-highlight {
  background-color: #f4fcf0;
}
.reply .author-content .user-avatar {
  display: inline-block;
  float: left;
}
.reply .author-content .user-avatar img {
  width: 30px;
  height: 30px;
  border-radius: 3px;
}
.reply .author-content .user-info {
  margin-left: 10px;
  display: inline-block;
}
.reply .author-content .user-info a {
  color: #08c;
  text-decoration: none;
}
.reply .author-content .user-info .reply-author {
  color: #666;
  text-decoration: none;
  font-size: 12px;
  font-weight: 700;
}
.reply .author-content .user-info .reply-time {
  font-size: 11px;
  margin-left: 6px;
}
.reply .author-content .user-info .reply-by-author {
  color: #fff;
  background-color: #6ba44e;
  padding: 2px;
  font-size: 12px;
  margin-left: 4px;
  display: none;
}
.reply .author-content .user-info .reply-by-author.display {
  display: inline-block;
}
.reply .author-content .user-action {
  float: right;
  margin-left: 20px;
  font-size: 15px;
  display: none;
}
.reply .author-content .user-action.display {
  display: block;
}
.reply .author-content .user-action .iconfont {
  color: #000;
  opacity: 0.4;
  cursor: pointer;
}
.reply .author-content .user-action .iconfont:hover {
  opacity: 0.6;
}
.reply .author-content .user-action .up-count {
  color: gray;
  margin-left: 6px;
}
.reply .reply-content {
  padding-left: 50px;
  color: #333;
}



