.header {
  padding: 10px;
  background-color: #f6f6f6;
  border-radius: 3px 3px 0 0;
  font-size: 14px;
}
.header .topic-tab {
  margin: 0 10px;
  color: #80bd01;
}
.header .topic-tab.current-tab {
  background-color: #80bd01;
  color: #fff;
  padding: 3px 4px;
  border-radius: 3px;
}
.inner.no-padding {
  padding: 0;
}
.inner {
  border-raidus: 0 0 3px 3px;
  background: #fff;
  line-height: 2em;
}
.inner .topic-list {
  margin: 0;
}
.inner .topic-list .info-nodata {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 800px;
  padding: 10px;
}
.inner .topic-list .cell {
  background: #fff;
  position: relative;
  padding: 10px;
  font-size: 14px;
  border-top: 1px solid #f0f0f0;
  cursor: pointer;
  overflow: hidden;
}
.inner .topic-list .cell:hover {
  background: #f5f5f5;
}
.inner .topic-list .cell:first-child {
  border-top: none;
}
.inner .topic-list .cell img {
  max-width: 100%;
  vertical-align: middle;
  border: 0;
}
.inner .topic-list .cell .user-avatar img {
  width: 30px;
  height: 30px;
  border-radius: 3px;
}
.inner .topic-list .cell .reply-count {
  width: 70px;
  display: inline-block;
  text-align: center;
}
.inner .topic-list .cell .reply-count .count-of-replies {
  color: #9e78c0;
}
.inner .topic-list .cell .reply-count .count-seperator {
  margin: 0 -3px;
  font-size: 10px;
}
.inner .topic-list .cell .reply-count .count-of-visits {
  font-size: 10px;
  color: #b4b4b4;
}
.inner .topic-list .cell .topic-title-wrapper {
  white-space: nowrap;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  color: #333;
}
.inner .topic-list .cell .topic-title-wrapper .tag {
  padding: 2px 4px;
  border-radius: 3px;
  font-size: 12px;
}
.inner .topic-list .cell .topic-title-wrapper .tag.put-top {
  background: #80bd01;
  color: #fff;
}
.inner .topic-list .cell .topic-title-wrapper .tag.tab-common {
  background-color: #e5e5e5;
  color: #999;
}
.inner .topic-list .cell .topic-title-wrapper .topic-title {
  margin-left: 4px;
  max-width: 70%;
  white-space: nowrap;
  display: inline-block;
  vertical-align: middle;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  overflow: hidden;
  color: #333;
  font-size: 16px;
  line-height: 30px;
}
.inner .topic-list .cell .last-time {
  font-size: 11px;
  display: inline-block;
  margin-left: 20px;
  color: #778087;
}
.inner .topic-list .cell .last-time .user-small-avatar {
  height: 18px;
  width: 18px;
  vertical-align: middle;
  margin-right: .5em;
  border-radius: 3px;
}



