.header-wrapper {
  width: 90%;
  padding: 5px;
  font-size: 14px;
  min-width: 960px;
  margin: 0 auto;
  max-width: 1400px;
}
.header-wrapper a.brand {
  display: inline-block;
  width: 120px;
  padding: 3px 20px;
  height: 34px;
  line-height: 34px;
  color: #ccc;
  font-weight: 700;
  margin-left: -20px;
}
.header-wrapper .nav.pull-right {
  float: right;
  margin: 0;
}
.header-wrapper .nav > li {
  float: left;
  line-height: 20px;
  list-style: none;
}
.header-wrapper .nav > li > a {
  display: block;
  text-shadow: none;
  color: #ccc;
  float: none;
  padding: 10px 15px;
  text-decoration: none;
}
.header-wrapper .nav > li > a:focus,
.header-wrapper .nav > li > a:hover {
  background: transparent;
}
.header-wrapper .nav > li > a:hover {
  color: #fff;
}



