.panel {
  font-size: 13px;
  margin-bottom: 13px;
}
.panel .cnode-app-download {
  text-align: center;
}
.panel .friendship-community img {
  width: 150px;
}
.panel .inner {
  padding: 10px;
  border-radius: 0 0 3px 3px;
  background-color: #fff;
  line-height: 2em;
}
.panel .inner ol {
  margin: 4px 0;
  list-style: none;
}
.panel .inner ol li {
  line-height: 2em;
}
.panel .inner ol .sep10 {
  height: 10px;
}
.panel .inner a {
  color: #778087;
  text-decoration: none;
}
.panel .inner a .span-info {
  display: inline-block;
  float: none;
  padding: 3px 10px;
  border: none;
  margin: 0;
  font-size: 14px;
  -webkit-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  cursor: pointer;
  letter-spacing: 2px;
  -webkit-box-shadow: none;
          box-shadow: none;
  border-radius: 3px;
  line-height: 2em;
  vertical-align: middle;
  color: #fff;
  background-color: #5bc0de;
}
.panel .header {
  color: #51585c;
  border-radius: 3px 3px 0 0;
  padding: 10px;
  background-color: #f6f6f6;
}
.panel .header .col-fade {
  color: #444;
}



