.markdown-text:first-child {
  margin-top: 0;
}
.markdown-text a {
  color: #08c;
}
.markdown-text img {
  height: auto;
  max-width: 100%;
  vertical-align: middle;
  border: 0;
}
.markdown-text p {
  font-size: 15px;
  line-height: 1.7em;
  overflow: auto;
  white-space: pre-wrap;
  word-wrap: break-word;
  margin: 1em 0;
}
.markdown-text blockquote {
  padding: 0 0 0 15px;
  margin: 0 0 20px;
  border-left: 5px solid #eee;
}
.markdown-text blockquote p {
  font-weight: 300;
}
.markdown-text h2 {
  font-size: 26px;
}
.markdown-text h1,
.markdown-text h2,
.markdown-text h3,
.markdown-text h4,
.markdown-text h5,
.markdown-text h6 {
  margin: 30px 0 15px;
  border-bottom: 1px solid #eee;
}
.markdown-text ol,
.markdown-text ul {
  padding: 0;
  margin: 0 0 10px 25px;
}
.markdown-text ol li,
.markdown-text ul li {
  font-size: 14px;
}
.markdown-text pre.prettyprint {
  font-size: 14px;
  border-radius: 0;
  padding: 10px 15px;
  border: none;
  margin: 20px -10px;
  border-width: 1px 0;
  background: #f7f7f7;
  -moz-tab-size: 4;
    -o-tab-size: 4;
       tab-size: 4;
}
.markdown-text code {
  color: inherit;
  white-space: pre-wrap;
  background-color: transparent;
  padding: 0;
  border: 0;
  font-family: Monaco, Menlo, Consolas, "Courier New", monospace;
  font-size: 12px;
  border-radius: 3px;
}



